














import { Component, Prop, Vue } from 'vue-property-decorator';
import PickerOne from '@/application/Common/View/CommonPickerOne.vue';
import CommonGameSelector from '@/application/Common/View/CommonGameSelector.vue';
import { GameData } from '@/globalInterface';

@Component({
  components: {
    PickerOne,
    CommonGameSelector
  }
})
export default class ReminderFilterBar extends Vue {
  private gamePickerShow?: boolean = false;
  private phaseData: GameData[] = Array.from(Array(10), (_, i) => ({id:i + 2, text: `${i + 2}期`}));
  private phaseChange(value: GameData) {
    this.$emit('index-change', value)
  }
  private gamesChange(value: string[]) {
    this.gamePickerShow = false;
    console.log(value);
  }
  private showPicker() {
    this.gamePickerShow = true;
    console.log(this.gamePickerShow);
  }
}
