





















import { Component, Prop, Vue } from 'vue-property-decorator';
import { GameData } from '@/globalInterface';

@Component({
  filters: {
    getTextList(arr: GameData[]) {
      if (arr) {
        return arr.map((x: GameData) => x.text);
      }
      return [];
    }
  }
})
export default class PickerOne extends Vue{
  @Prop({type: String, default: ''}) public readonly title!: string;
  @Prop({type: Array, default: () => []}) public readonly dataList?: GameData[];
  @Prop({type: Boolean, default: false}) public readonly sendOnInit!: boolean;
  private showPicker?: boolean = false;
  private selectedId?: number = 0; 
  private getText(): string {
    if (this.dataList) {
      if (this.dataList[this.selectedId || 0]) {
        const game: GameData = this.dataList[this.selectedId || 0];
        return game.text;
      }
    }
    return '';
  }
  private emitData(data: GameData) {
    this.$emit('index-change', data);
  }
  private togglePicker() {
    this.showPicker = !this.showPicker;
  }
  private onConfirm(value: string, index: number) {
    const data = this.dataList || [];
    const selectedGame: GameData = data[index];
    if (selectedGame) {
      this.selectedId = index;
      this.togglePicker();
      this.emitData(selectedGame);
    }
  }
  private mounted() {
    if (this.sendOnInit) {
      const data = this.dataList || [];
      this.emitData(data[this.selectedId || 0]);
    }
  }
}
